import { Component, OnInit } from '@angular/core';
//for datepicker starts 
import { NgbDateStruct, NgbCalendar } from '@ng-bootstrap/ng-bootstrap';
//for datepicker ends 
//for popup modal starts 
import { NgbModal, ModalDismissReasons, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
//for popup modal endss 

@Component( {
    selector: 'app-lrs-short-and-extra',
    templateUrl: './lrs-short-and-extra.component.html',
    styleUrls: ['./lrs-short-and-extra.component.css']
} )
export class LrsShortAndExtraComponent implements OnInit {

    closeResult: string;
    otherView = true;
    constructor( private modalService: NgbModal ) { }
    //for popup modal starts 
    open2( content ) {
        this.modalService.open( content, { centered: true } ).result.then(
            result => {
                this.closeResult = `Closed with: ${result}`;
            },
            reason => {
                this.closeResult = `Dismissed ${this.getDismissReason( reason )}`;
            }
        );
    }
    private getDismissReason( reason: any ): string {
        if ( reason === ModalDismissReasons.ESC ) {
            return 'by pressing ESC';
        } else if ( reason === ModalDismissReasons.BACKDROP_CLICK ) {
            return 'by clicking on a backdrop';
        } else {
            return `with: ${reason}`;
        }
    }
    //for popup modal ends 

    markAsMode( markAs: string ) {
        if ( markAs === 'short' ) {
            this.otherView = true;
        } else if
  ( markAs === 'extra' ) {
            this.otherView = true;
        } else if
        ( markAs === 'openCondition' ) {
            this.otherView = true;
        } else if
        ( markAs === 'wetCondition' ) {
            this.otherView = true;
        } else if
        ( markAs === 'invoiceClear' ) {
            this.otherView = false;

        }
    }
    ngOnInit() {
    }
}
